//colors
$color_bay_of_many_approx: #2c287f;
$color_lightning_yellow_approx: #f9bf20;
$color_lucky_point_approx: #1b1865;
$color_blue_marguerite_approx: #726dcd;
$color_gray_nurse_approx: #e8e8e8;
$color_shuttle_gray_approx: #666670;
$alabaster: #fafafa;
$color_bon_jour_approx: #e1e1e1;
$color_celeste_approx: #cccccc;
$color_moody_blue_approx: #7e7ad1;
$black_15: rgba(0, 0, 0, 0.15);
$color_ocean_green_approx: #37b880;
$black_7: rgba(0, 0, 0, 0.07);
$color_mountain_mist_approx: #959595;
$color_jacksons_purple_approx: #2c2481;
$color_valencia_approx: #de3a43;
$color_concrete_approx: #f3f3f3;
$color_quill_gray_approx: #d4d4d4;
$white_15: rgba(white, 0.15);
$black_5: rgba(0, 0, 0, 0.05);
$color_alto_approx: #dddddd;
$color_fair_pink_approx: #ffe7e8;
$color_shilo_approx: #edb4b7;

body {
	font-family: 'Nexa Regular';
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.nav-up {
	top: -82px;
}
.overscroll {
	overflow: scroll;
	-webkit-overflow-scrolling: touch;
}
.popover {
	&.time {
		.overscroll {
			background-color: $color_lucky_point_approx;
			opacity: 0;
			@include transition(opacity 600ms cubic-bezier(0.645, 0.045, 0.355, 1));
		}
		img.spin {
			position: absolute;
			top: 50%;
			left: 50%;
			@include transform(translate(-50%, -50%));
			width: 100px;
			fill: $color_lightning_yellow_approx;
			z-index: -1;
		}
	}
	&.show.time {
		opacity: 1;
		pointer-events: all;
		bottom: 0!important;
		ul.datepicker {
			@include transition(all 300ms cubic-bezier(0.645, 0.045, 0.355, 1));
			opacity: 1;
		}
	}
	&.dest ul.results {
		position: absolute;
		background-color: white;
		width: 100%;
		padding-left: inherit;
		padding-right: inherit;
		left: 0;
	}
}
div[role="secondary"] {
	margin: 0;
	height: 0;
	margin-top: 85px;
	display: none;
	h1 {
		color: $color_blue_marguerite_approx;
		font-size: 30px;
		padding: 0;
		line-height: 50px;
		margin-bottom: 30px;
		u {
			text-decoration: none;
			border-bottom: 1px solid currentColor;
		}
	}
}
.theme-showcase {
	> p > .btn {
		margin: 5px 0;
	}
	.navbar .container {
		width: auto;
	}
}
ul {
	&.results {
		padding: 0;
		@include transition(all 600ms cubic-bezier(0.645, 0.045, 0.355, 1));
		opacity: 0;
		&:nth-of-type(1) {
			opacity: 1;
			li:after {
				background-image: url('../img/location.svg');
			}
		}
		li {
			list-style: none;
			font-family: 'Nexa Heavy';
			font-size: 20px;
			color: $color_blue_marguerite_approx;
			border-bottom: 1px solid $color_gray_nurse_approx;
			padding: 25px 0 20px 45px;
			position: relative;
			&.noresult {
				text-align: center;
				padding: 50px 0 0;
				border-bottom: none;
				&:after {
					display: none;
				}
			}
			img {
				width: 100px;
				margin-bottom: 20px;
			}
			&:after {
				content: "";
				display: block;
				position: absolute;
				width: 20px;
				height: 20px;
				background-size: contain;
				background-position: center;
				background-repeat: no-repeat;
				top: 50%;
				@include transform(translateY(-50%));
				left: 5px;
			}
			.distance {
				position: absolute;
				right: 0;
				color: $color_shuttle_gray_approx;
				font-size: 14px;
				bottom: 20px;
				font-family: 'Nexa Regular';
			}
			p {
				margin-bottom: 0;
			}
			span {
				font-family: 'Nexa Regular';
				font-size: 16px;
				color: $color_shuttle_gray_approx;
			}
		}
		&:nth-of-type(2) li:after {
			background-image: url('../img/pin.svg');
		}
		&:nth-of-type(3) li:after {
			background-image: url('../img/saved.svg');
		}
		&:nth-of-type(4) li:after {
			background-image: url('../img/pin.svg');
		}
	}
	&.datepicker {
		list-style-type: none;
		padding: 0;
		margin: 0;
		overflow: scroll;
		white-space: nowrap;
		-webkit-scroll-snap-type: mandatory;
		-ms-scroll-snap-type: mandatory;
		scroll-snap-type: mandatory;
		-webkit-scroll-snap-points-x: repeat(50%);
		-ms-scroll-snap-points-x: repeat(50%);
		scroll-snap-points-x: repeat(50%);
		scroll-behavior: smooth;
		-webkit-overflow-scrolling: touch;
		position: fixed;
		width: 100%;
		opacity: 0;
		padding: 18px 0 40px;
		li {
			width: 50%;
			display: inline-block;
			text-align: center;
			margin-right: -4px;
		 
			box-sizing:border-box;
			.date {
				color: $color_moody_blue_approx;
				font-size: 16px;
				font-family: 'Nexa Heavy';
				border-radius: 32px;
				padding: 5px 0;
				position: relative;
				span {
					font-family: 'Nexa Regular';
				}
			}
			&.highlight .date {
				color: white;
				background-color: $color_bay_of_many_approx;
				&:after {
					position: absolute;
					content: "";
					display: block;
					left: 50%;
					@include transform(translateX(-50%));
					width: 0;
					height: 0;
					border-left: 15px solid transparent;
					border-right: 15px solid transparent;
					border-top: 15px solid $color_bay_of_many_approx;
				}
			}
		}
	}
	&.menu {
		list-style-type: none;
		padding: 0;
		margin: 0;
		overflow: scroll;
		white-space: nowrap;
		-webkit-scroll-snap-type: mandatory;
		-ms-scroll-snap-type: mandatory;
		scroll-snap-type: mandatory;
		-webkit-scroll-snap-points-x: repeat(50%);
		-ms-scroll-snap-points-x: repeat(50%);
		scroll-snap-points-x: repeat(50%);
		scroll-behavior: smooth;
		-webkit-overflow-scrolling: touch;
		position: fixed;
		width: 100%;
		background-color: $alabaster;
		border-top: 3px solid $color_bon_jour_approx;
		border-bottom: 1px solid $color_gray_nurse_approx;
		padding: 18px 0;
		z-index: 2;
		@include transition(all 300ms cubic-bezier(0.645, 0.045, 0.355, 1));
		opacity: 1;
		li {
			width: 50%;
			display: inline-block;
			text-align: center;
			margin-right: -4px;
			font-size: 24px;
			color: $color_celeste_approx;
			&.highlight {
				color: $color_shuttle_gray_approx;
			}
		}
	}
	&.weekdays {
		padding-left: 20px;
		padding-right: 20px;
		height: 50px;
		border-bottom: 1px solid $color_alto_approx;
		background-color: $alabaster;
		margin: 0;
		li {
			width: calc(100% / 7);
			list-style: none;
			display: inline-block;
			text-align: center;
			margin-right: -4px;
			color: $color_shuttle_gray_approx;
			padding-top: 23px;
		}
	}
}
.cards {
	position: relative;
	margin-top: 112px;
	@include transition(all 600ms cubic-bezier(.76, -0.15, .2, 1.2));
	ul {
		padding: 0;
		margin-bottom: 0;
		@include transition(all 300ms cubic-bezier(0.645, 0.045, 0.355, 1));
		overflow: hidden;
		height: calc(100% - 400px);
		li {
			width: calc(100vw - 50px);
			margin: auto;
			background-color: white;
			border-radius: 6px;
			padding: 20px;
			@include transition(all 600ms cubic-bezier(.76, -0.15, .2, 1.2));
			box-shadow: 0 -5px 9px 0 $black_15;
			position: relative;
			@include transform(perspective(401px) rotateX(-7deg) scale(0.92));
			@include transform-style(preserve-3d);
			&:first-of-type {
				margin-top: 0!important;
			}
			&:after {
				content: "Cheapest";
				position: absolute;
				right: 20px;
				padding: 0 10px;
				background-color: $color_ocean_green_approx;
				color: white;
				font-size: 10px;
				line-height: 20px;
				top: 0;
				//Instead of the line below you could use @include border-bottom-left-radius($radius)
				border-bottom-left-radius: 2px;
				//Instead of the line below you could use @include border-bottom-right-radius($radius)
				border-bottom-right-radius: 2px;
			}
			&.scroll {
				@include transition(none !important);
			}
			&.current {
				@include transform(perspective(0px) rotateX(0deg) scale(1) !important);
				box-shadow: 0 -10px 15px 0 $black_7!important;
				&:last-of-type {
					margin-bottom: 20px !important;
				}
			}
			&.push {
				margin-top: 20px!important;
			}
			.deptime {
				height: 80px;
				border-bottom: 2px dashed $color_gray_nurse_approx;
				position: relative;
				margin: 7px -20px 25px;
				padding: 0 20px;
				&:before {
					position: absolute;
					content: "";
					display: block;
					height: 18px;
					width: 18px;
					//Instead of the line below you could use @include border-radius($radius, $vertical-radius)
					border-radius: 50%;
					background-color: $color_lucky_point_approx;
					bottom: -9px;
					left: -9px;
				}
				&:after {
					position: absolute;
					content: "";
					display: block;
					height: 18px;
					width: 18px;
					//Instead of the line below you could use @include border-radius($radius, $vertical-radius)
					border-radius: 50%;
					background-color: $color_lucky_point_approx;
					bottom: -9px;
					right: -9px;
				}
				p {
					color: $color_bay_of_many_approx;
					font-size: 20px;
					position: relative;
					margin: 0;
					width: 70%;
					display: inline-block;
					margin-right: -4px;
					vertical-align: top;
					span {
						margin: 0 5px;
					}
					img {
						height: 8px;
					}
				}
				.price {
					font-size: 24px;
					color: $color_bay_of_many_approx;
					font-family: 'Nexa Heavy';
					width: 30%;
					display: inline-block;
					margin-right: -4px;
					text-align: right;
					vertical-align: top;
					position: relative;
				}
				.duration {
					color: $color_mountain_mist_approx;
					font-size: 12px;
					font-family: 'Nexa Regular';
					position: relative;
					display: inline-block;
					margin-right: -4px;
					color: $color_mountain_mist_approx;
					font-size: 12px;
					font-family: 'Nexa Regular';
					position: relative;
					display: inline-block;
					margin-right: -4px;
					text-align: right;
				}
				.transfer {
					color: $color_mountain_mist_approx;
					font-size: 12px;
					font-family: 'Nexa Regular';
					position: relative;
					display: inline-block;
					margin-right: -4px;
					text-align: right;
					margin-left: 10px;
				}
				.travellers {
					color: $color_jacksons_purple_approx;
					font-size: 12px;
					text-align: right;
					display: inline-block;
					right: 20px;
					position: absolute;
				}
			}
			.hero {
				position: relative;
				margin-bottom: 15px;
				&:after {
					content: "";
					position: absolute;
					display: block;
					width: 54px;
					height: 50px;
					top: 0;
					background-image: url('../img/sid.png');
					background-size: cover;
					@include transform(translateX(-100%));
					left: 100%;
				}
				p {
					color: $color_bay_of_many_approx;
					font-size: 20px;
					font-family: 'Nexa Heavy';
					margin-bottom: 0;
					width: 100%;
					span {
						color: $color_valencia_approx;
						font-size: 18px;
					}
				}
			}
			&.previous {
				opacity: 1;
				@include animation(wangiReject 0.5s forwards);
				@include transform-origin(0 0);
				z-index: 9;
			}
			.facilities {
				position: relative;
				border-top: 1px solid $color_concrete_approx;
				border-bottom: 1px solid $color_concrete_approx;
				padding: 18px 0;
				text-align: center;
				img {
					position: relative;
					height: 23px;
					margin: 0 10px;
				}
			}
			.timetable {
				margin-top: 32px;
				margin-left: 20px;
				border-left: 2px dashed $color_quill_gray_approx;
				margin-bottom: 25px;
				.departure {
					color: $color_shuttle_gray_approx;
					font-size: 12px;
					font-family: 'Nexa Regular';
					margin-left: 43px;
					position: relative;
					margin-top: 22px;
					&:before {
						content: "";
						position: absolute;
						display: block;
						width: 14px;
						height: 14px;
						box-sizing: border-box;
						-moz-box-sizing: border-box;
						-webkit-box-sizing: border-box;
						top: 50%;
						margin-top: -12px;
						@include transform(translateY(-50%));
						margin-left: -51px;
						border: 2px solid $color_lightning_yellow_approx;
						border-radius: 50%;
						background-color: white;
					}
					span {
						color: $color_moody_blue_approx;
						font-size: 13px;
						font-family: 'Nexa Heavy';
					}
				}
				.destination {
					color: $color_shuttle_gray_approx;
					font-size: 12px;
					font-family: 'Nexa Regular';
					margin-left: 43px;
					position: relative;
					margin-top: 22px;
					&:before {
						content: "";
						position: absolute;
						display: block;
						width: 14px;
						height: 14px;
						box-sizing: border-box;
						-moz-box-sizing: border-box;
						-webkit-box-sizing: border-box;
						top: 50%;
						margin-top: -12px;
						@include transform(translateY(-50%));
						margin-left: -51px;
						border: 2px solid $color_lightning_yellow_approx;
						border-radius: 50%;
						background-color: white;
					}
					&:after {
						content: "";
						display: block;
						position: absolute;
						width: 4px;
						height: 30px;
						background-color: white;
						left: -46px;
						top: 12px;
					}
					span {
						color: $color_moody_blue_approx;
						font-size: 13px;
						font-family: 'Nexa Heavy';
					}
				}
				.change {
					color: $color_shuttle_gray_approx;
					font-size: 12px;
					font-family: 'Nexa Regular';
					margin-left: 43px;
					position: relative;
					margin-top: 22px;
					&:before {
						content: "";
						position: absolute;
						display: block;
						width: 10px;
						height: 10px;
						//Instead of the line below you could use @include box-sizing($bs)
						box-sizing: border-box;
						-moz-box-sizing: border-box;
						-webkit-box-sizing: border-box;
						top: 50%;
						margin-top: -10px;
						@include transform(translateY(-50%));
						margin-left: -49px;
						background-image: url('../img/transfer.jpg');
						background-size: contain;
					}
					span {
						color: $color_moody_blue_approx;
						font-size: 13px;
						font-family: 'Nexa Heavy';
					}
				}
			}
		}
	}
	.availability {
		color: $color_shuttle_gray_approx;
		margin-top: 10px;
		font-size: 12px;
	}
}
footer {
	background-color: $color_bay_of_many_approx;
	padding-top: 125px;
	padding-bottom: 170px;
	ul {
		width: 25%;
		float: left;
		padding: 0;
		li {
			color: white;
			list-style: none;
			line-height: 22px;
			opacity: 0.8;
			&:nth-child(1) {
				font-family: 'Nexa Heavy';
				margin-bottom: 15px;
				opacity: 1;
			}
		}
	}
}
@media(max-width: 767px) {
	footer {
		background-color: $color_bay_of_many_approx;
		padding-top: 90px;
		padding-bottom: 40px;
		ul {
			width: 100%;
			margin-bottom: 50px;
			li {
				text-align: center;
			}
		}
	}
}
.switchname {
	color: $color_shuttle_gray_approx;
	font-size: 18px;
	margin-top: 34px;
	line-height: 37px;
	float: left;
	padding-left: 20px;
}
.onoffswitch {
	position: relative;
	width: 84px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	margin-top: 34px;
	color: white;
	font-family: 'Nexa Heavy';
	font-size: 20px;
	line-height: 20px;
	margin-right: 20px;
	float: right;
	margin-bottom: 25px;
}
.onoffswitch-checkbox {
	display: none;
	&:checked + .onoffswitch-label {
		.onoffswitch-inner {
			margin-left: 0;
		}
		&:before {
			right: 0;
			background-color: white;
			box-shadow: 0 3px 0 0 $black_5;
		}
	}
}
.onoffswitch-label {
	display: block;
	overflow: hidden;
	cursor: pointer;
	height: 32px;
	padding: 0;
	line-height: 20px;
	border: 0 solid white;
	border-radius: 30px;
	background-color: $color_bay_of_many_approx;
	box-shadow: inset 0 3px 0 $white_15;
	padding-top: 8px;
	padding-left: 39px;
	&:before {
		content: "";
		display: block;
		width: 36px;
		height: 36px;
		margin: -2px;
		background: white;
		position: absolute;
		top: 0;
		bottom: 0;
		right: 52px;
		border-radius: 33px;
		box-shadow: 0 3px 0 0 $black_5;
		border: 1px solid $color_celeste_approx;
		box-sizing: border-box;
		-moz-box-sizing: border-box;
		-webkit-box-sizing: border-box;
	}
}
.toggle {
	position: relative;
	width: 100%;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	margin-top: 17px;
	color: white;
	font-family: 'Nexa Heavy';
	font-size: 20px;
	height: 46px;
	margin-right: 20px;
	margin-bottom: 25px;
	border-radius: 23px;
	border: 1px solid $color_celeste_approx;
	.switch {
		width: 50%;
		height: 50px;
		background-color: $color_bay_of_many_approx;
		border-radius: 25px;
		position: absolute;
		@include transform(translate(100%, -100%));
		@include transition(all 300ms cubic-bezier(0.86, 0, 0.07, 1));
		margin-top: 1px;
	}
	.element {
		width: 50%;
		line-height: 46px;
		color: $color_celeste_approx;
		display: inline-block;
		margin-right: -4px;
		font-size: 16px;
		text-align: center;
		position: relative;
		z-index: 2;
		@include transition(all 300ms cubic-bezier(0.86, 0, 0.07, 1));
		&.current {
			color: white;
		}
	}
}
.accordion {
	max-height: 70px;
	overflow: hidden;
	border-top: 1px solid $color_celeste_approx;
	border-bottom: 1px solid $color_celeste_approx;
	margin-top: 33px;
	@include transition(all 300ms cubic-bezier(0.86, 0, 0.07, 1));
	background-color: $alabaster;
	&.expand {
		max-height: 7000px;
	}
	p.accordionToggle {
		text-align: center;
		color: $color_moody_blue_approx;
		font-size: 20px;
		font-family: 'Nexa Heavy';
		line-height: 70px;
	}
}
#calendar {
	padding-bottom: 88px;
}
#ui-datepicker-div {
	top: 68px!important;
	left: 0!important;
	width: 100%!important;
	//Instead of the line below you could use @include border-radius($radius, $vertical-radius)
	border-radius: none!important;
}
.ui-datepicker-group {
	width: 100%!important;
}
.ui-datepicker-title {
	margin-top: 23px;
}
.inputerror {
	color: $color_valencia_approx;
	font-size: 16px;
	font-family: 'Nexa Heavy';
	margin-top: 10px;
	&.hidden {
		display: none;
	}
}
#promo {
	&.alert {
		background-color: $color_fair_pink_approx;
		color: $color_valencia_approx;
		border-color: $color_valencia_approx;
	}
	&.alert::-webkit-input-placeholder {
		color: $color_shilo_approx;
	}
}
.infopopup {
	position: absolute;
	right: 20px;
	margin-top: 47px;
	width: 25px;
	height: 25px;
	img {
		width: 100%;
	}
}
.cardelement {
	position: absolute;
	@include transform(translate(-50%, -50%));
	top: 50%;
	left: 50%;
	z-index: 9;
	width: 280px;
	height: 365px;
	//Instead of the line below you could use @include border-radius($radius, $vertical-radius)
	border-radius: 2px;
	img {
		width: 100%;
	}
}
form p {
	margin: 0;
}
